import _forEach from 'lodash.foreach'
import _clonedeep from 'lodash.clonedeep'

export default {
  props: {
    /**
     * @description dialog
     */
    formOptions: {
      type: Object,
      default: null
    },
    /**
     * @description dialog
     */
    addTitle: {
      type: String,
      default: ''
    },
    /**
     * @description dialog
     */
    editTitle: {
      type: String,
      default: ''
    },
    /**
     * @description 
     */
    addTemplate: {
      type: Object,
      default: null
    },
    /**
     * @description 
     */
    editTemplate: {
      type: Object,
      default: null
    },
    /**
     * @description 
     */
    formRules: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      /**
       * @description dialog
       */
      showDialog: false,
      /**
       * @description 
       */
      formData: {},
      /**
       * @description 
       */
      formMode: 'edit'
    }
  },
  methods: {
    /**
     * @description 
     */
    handleDialogSave () {
      this.$refs.form.validate((valid) => {
        if (!valid) {
          return false
        }
        let rowData = {}
        if (this.formMode === 'edit') {
          rowData = _clonedeep(this.editDataStorage)
          _forEach(this.formData, (value, key) => {
            this._set(rowData, key, value)
          })
          this.$emit('row-edit', {
            index: this.editIndex,
            row: rowData
          }, (param = null) => {
            if (param === false) {
              this.closeDialog()
              return
            }
            this.handleDialogSaveDone({
              ...rowData,
              ...param
            })
          })
        } else if (this.formMode === 'add') {
          _forEach(this.formData, (value, key) => {
            this._set(rowData, key, value)
          })
          this.$emit('row-add', rowData, (param = null) => {
            if (param === false) {
              this.closeDialog()
              return
            }
            this.handleDialogSaveDone({
              ...rowData,
              ...param
            })
          })
        }
      })
    },
    /**
     * @description 
     */
    handleDialogCancel (done) {
      this.$emit('dialog-cancel', done)
    },
    /**
     * @description 
     */
    handleDialogSaveDone (rowData) {
      if (this.formMode === 'edit') {
        this.updateRow(this.editIndex, rowData)
        this.editDataStorage = {}
      } else if (this.formMode === 'add') {
        this.addRow(rowData)
      }
      this.closeDialog()
    },
    /**
     * @description 
     */
    closeDialog () {
      this.showDialog = false
    }
  }
}



