export default {
  methods: {
    /**
     * @description 
     */
    handleRemove (index, indexrow) {
      if ((!('confirm' in this.rowHandle.remove)) || this.rowHandle.remove.confirm === true) {
        this.$confirm(this.handleAttribute(this.rowHandle.remove.confirmText, ''), this.handleAttribute(this.rowHandle.remove.confirmTitle, ''), {
          confirmButtonText: this.handleAttribute(this.rowHandle.remove.confirmButtonText, ''),
          cancelButtonText: this.handleAttribute(this.rowHandle.remove.cancelButtonText, ''),
          type: this.handleAttribute(this.rowHandle.remove.type, 'error')
        }).then(() => {
          this.$emit('row-remove', {index, row: indexrow}, () => {
            this.handleRemoveDone(index)
          })
        })
      } else {
        this.$emit('row-remove', {index, row: indexrow}, () => {
          this.handleRemoveDone(index)
        })
      }
    },
    /**
     * @description 
     */
    handleRemoveDone (index) {
      this.removeRow(index)
    }
  }
}



